export function GardenFlower({
  size = 28,
  color = "#f43f5e",
  tilt = 0,
}: {
  size?: number;
  color?: string;
  tilt?: number;
}) {
  return (
    <svg
      width={size}
      height={size * 1.9}
      viewBox="0 0 32 60"
      xmlns="http://www.w3.org/2000/svg"
      aria-hidden
      style={{ display: "block", transform: `rotate(${tilt}deg)` }}
    >
      {/* stem */}
      <path
        d="M16 22v34"
        stroke="#16a34a"
        strokeWidth="2.5"
        strokeLinecap="round"
      />
      {/* leaves */}
      <path
        d="M16 34c-6-3-10 1-11 6 4-1 7 1 11 4"
        fill="#22c55e"
        fillOpacity="0.85"
      />
      <path
        d="M16 40c6-3 10 1 11 6-4-1-7 1-11 4"
        fill="#84cc16"
        fillOpacity="0.8"
      />
      {/* flower head */}
      <circle cx="16" cy="14" r="7" fill={color} />
      <circle cx="16" cy="14" r="3" fill="#fff" />
    </svg>
  );
}
