// src/app/components/decor/Garden.tsx

import React from "react";
import { GardenFlower } from "./GardenFlower";
import { GrassBlade } from "./GrassBlade";



function FlowerCluster({
  colors = ["#f43f5e", "#e11d48", "#fb7185"],
  flip = false,
}: {
  colors?: string[];
  flip?: boolean;
}) {
  const sizes = [22, 26, 30, 24];
  return (
    <div className={`flex items-end gap-2 ${flip ? "scale-x-[-1]" : ""}`}>
      <GrassBlade h={42} bend={8} />
      <GardenFlower size={sizes[0]} color={colors[0]} tilt={-3} />
      <GrassBlade h={28} bend={4} />
      <GardenFlower size={sizes[2]} color={colors[2]} tilt={2} />
      <GrassBlade h={36} bend={10} />
      <GardenFlower size={sizes[1]} color={colors[1]} tilt={-1} />
    </div>
  );
}

/* ------------------ Layout helpers ------------------ */

function MeadowRow({
  count = 8,
  flip = false,
  scale = 1,
  colors = ["#f43f5e", "#e11d48", "#fb7185"],
}: {
  count?: number;
  flip?: boolean;
  scale?: number;
  colors?: string[];
}) {
  // Evenly distribute clusters across a responsive row
  return (
    <div
      className="pointer-events-none w-full grid"
      style={{
        gridTemplateColumns: `repeat(${count}, minmax(0, 1fr))`,
        transform: `scale(${scale})`,
      }}
      aria-hidden
    >
      {Array.from({ length: count }).map((_, i) => (
        <div
          key={i}
          className={`garden-sway ${i % 2 ? "sway-fast" : "sway-slow"} flex items-end justify-center ${
            flip && i % 2 ? "scale-x-[-1]" : ""
          }`}
          style={{ animationDelay: `${(i % 6) * 0.3}s` }}
        >
          <FlowerCluster colors={colors} flip={flip && i % 2 === 0} />
        </div>
      ))}
    </div>
  );
}

function VinesTop() {
  // Minimal top-corner accents to frame upper area
  return (
    <div className="pointer-events-none absolute inset-x-0 top-0 z-0" aria-hidden>
      <div className="absolute left-0 top-0 p-2 garden-drift" style={{ animationDuration: "18s" }}>
        <GardenFlower size={22} color="#86efac" tilt={-8} />
      </div>
      <div
        className="absolute left-[9%] top-2 hidden sm:block p-2 garden-drift"
        style={{ animationDuration: "22s", animationDelay: "-6s" }}
      >
        <GardenFlower size={20} color="#a7f3d0" tilt={-4} />
      </div>
      <div className="absolute right-0 top-0 p-2 garden-drift" style={{ animationDuration: "19s" }}>
        <GardenFlower size={22} color="#86efac" tilt={7} />
      </div>
      <div
        className="absolute right-[10%] top-3 hidden sm:block p-2 garden-drift"
        style={{ animationDuration: "23s", animationDelay: "-8s" }}
      >
        <GardenFlower size={20} color="#a7f3d0" tilt={5} />
      </div>
    </div>
  );
}

/* ------------------ Main exported frames ------------------ */

export function GardenFrame() {
  // Multi-layered decorative garden with broad coverage and varied sway speeds.
  return (
    <div className="pointer-events-none absolute inset-0 z-0" aria-hidden>
      {/* soft, taller ground gradient across bottom ~45% of screen */}
      <div
        className="absolute inset-x-0 bottom-0"
        style={{
          height: "45%",
          background:
            "linear-gradient(to top, rgba(22,163,74,0.14), rgba(22,163,74,0.04), rgba(22,163,74,0))",
        }}
      />

      {/* Background layer: large, lighter clusters spread tall for coverage */}
      <div className="absolute inset-x-0 bottom-0 h-[58%] opacity-40">
        <div className="mx-auto max-w-7xl px-2 space-y-6">
          <MeadowRow count={7} scale={1.05} colors={["#86efac", "#bbf7d0", "#a7f3d0"]} />
          <MeadowRow count={6} flip scale={1.1} colors={["#bbf7d0", "#a7f3d0", "#86efac"]} />
        </div>
      </div>

      {/* Midground layer */}
      <div className="absolute inset-x-0 bottom-0 h-[46%] opacity-60">
        <div className="mx-auto max-w-6xl px-3 space-y-4">
          <MeadowRow count={9} scale={1.0} colors={["#fda4af", "#fb7185", "#fca5a5"]} />
          <MeadowRow count={8} flip scale={1.0} colors={["#fb7185", "#f43f5e", "#e11d48"]} />
        </div>
      </div>

      {/* Foreground layer: densest at the base for a lush look */}
      <div className="absolute inset-x-0 bottom-0 h-[34%] opacity-85">
        <div className="mx-auto max-w-5xl px-4 space-y-3">
          <MeadowRow count={11} scale={0.98} colors={["#f43f5e", "#e11d48", "#fb7185"]} />
          <div className="flex items-end justify-between px-1">
            <div className="garden-sway sway-med">
              <FlowerCluster />
            </div>
            <div className="garden-sway sway-med">
              <FlowerCluster flip />
            </div>
          </div>
        </div>
      </div>

      {/* Corner anchors for ultra-wide */}
      <div className="garden-sway sway-med absolute left-2 bottom-1 hidden md:block">
        <FlowerCluster />
      </div>
      <div className="garden-sway sway-med absolute right-2 bottom-1 hidden md:block">
        <FlowerCluster flip />
      </div>

      {/* Top accents */}
      <VinesTop />

      <style jsx>{`
        .garden-sway {
          animation: garden-sway 5.6s ease-in-out infinite;
        }
        .sway-slow {
          animation-duration: 6.6s;
        }
        .sway-med {
          animation-duration: 5.2s;
        }
        .sway-fast {
          animation-duration: 4.2s;
        }

        @keyframes garden-sway {
          0%,
          100% {
            transform: translateY(0) rotate(-1.2deg);
          }
          50% {
            transform: translateY(-3px) rotate(1.2deg);
          }
        }

        .garden-drift {
          animation: garden-drift 20s linear infinite;
        }
        @keyframes garden-drift {
          0% {
            transform: translateX(0);
          }
          50% {
            transform: translateX(6px);
          }
          100% {
            transform: translateX(0);
          }
        }
      `}</style>
    </div>
  );
}

/* ------------------ Optional bottom ribbon ------------------ */

function FlowerSVG({ size = 22, color = "#16a34a" }: { size?: number; color?: string }) {
  return (
    <svg
      width={size}
      height={size}
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      aria-hidden
    >
      <path
        d="M12 2.5c1.3 0 2.5.9 3 2.2.5-1.3 1.7-2.2 3-2.2 1.9 0 3.5 1.6 3.5 3.5 0 1.5-1 2.8-2.4 3.3 1.4.5 2.4 1.8 2.4 3.3 0 1.9-1.6 3.5-3.5 3.5-1.3 0-2.5-.9-3-2.2-.5 1.3-1.7 2.2-3 2.2-1.3 0-2.5-.9-3-2.2-.5 1.3-1.7 2.2-3 2.2-1.9 0-3.5-1.6-3.5-3.5 0-1.5 1-2.8 2.4-3.3C3 8.8 2 7.5 2 6c0-1.9 1.6-3.5 3.5-3.5 1.3 0 2.5.9 3 2.2.5-1.3 1.7-2.2 3-2.2Z"
        fill={color}
        fillOpacity="0.85"
      />
      <circle cx="12" cy="12" r="2.5" fill="#ffffff" fillOpacity="0.9" />
    </svg>
  );
}

/** A seamless marquee of tiny flowers drifting across the bottom. */
export function FlowersRibbon() {
  const palette = ["#16a34a", "#22c55e", "#84cc16", "#65a30d"];
  const sizes = [16, 18, 20, 22, 24];
  const row = new Array(24).fill(0);

  return (
    <div className="pointer-events-none fixed inset-x-0 bottom-0 z-10" style={{ height: 56 }} aria-hidden>
      {/* subtle gradient to help contrast */}
      <div
        className="absolute inset-0"
        style={{
          background: "linear-gradient(to top, rgba(22,163,74,0.08), rgba(22,163,74,0))",
        }}
      />
      <div className="relative h-full overflow-hidden">
        <div className="flowers-track will-change-transform" style={{ animationDuration: "28s" }}>
          {row.map((_, i) => (
            <div key={"a" + i} className="flowers-item">
              <FlowerSVG size={sizes[i % sizes.length]} color={palette[i % palette.length]} />
            </div>
          ))}
        </div>
        <div
          className="flowers-track will-change-transform"
          style={{ animationDuration: "28s", animationDelay: "-14s" }}
        >
          {row.map((_, i) => (
            <div key={"b" + i} className="flowers-item">
              <FlowerSVG size={sizes[(i + 2) % sizes.length]} color={palette[(i + 1) % palette.length]} />
            </div>
          ))}
        </div>
      </div>

      <style jsx>{`
        .flowers-track {
          position: absolute;
          left: 0;
          top: 8px;
          display: flex;
          gap: 18px;
          padding-left: 8px;
          width: 200%;
          animation-name: flowers-marquee;
          animation-timing-function: linear;
          animation-iteration-count: infinite;
        }
        .flowers-item {
          transform: translateY(0);
          animation: flowers-bob 3.8s ease-in-out infinite;
        }
        .flowers-item:nth-child(3n) {
          animation-duration: 4.4s;
        }
        .flowers-item:nth-child(4n) {
          animation-duration: 3.2s;
        }
        @keyframes flowers-marquee {
          from {
            transform: translateX(0%);
          }
          to {
            transform: translateX(-50%);
          }
        }
        @keyframes flowers-bob {
          0%,
          100% {
            transform: translateY(0px) rotate(0deg);
          }
          50% {
            transform: translateY(-3px) rotate(2deg);
          }
        }
      `}</style>
    </div>
  );
}