export function GrassBlade({ h = 32, bend = 6 }: { h?: number; bend?: number }) {
  const top = 56 - h;
  return (
    <svg
      width={12}
      height={56}
      viewBox={`0 ${top} 12 ${h}`}
      xmlns="http://www.w3.org/2000/svg"
      aria-hidden
    >
      <path
        d={`M6 ${top + h} C ${6 - bend} ${top + h - h / 2}, ${6 + bend} ${
          top + h - h / 2
        }, 6 ${top}`}
        stroke="#16a34a"
        strokeWidth="2"
        fill="none"
        strokeLinecap="round"
      />
    </svg>
  );
}
